.dropdown-menu {
  width: 500px;
  position: absolute;
  bottom: 100px;
  list-style: none;
  text-align: start;
  border-radius: 10px;
}

.dropdown-menu li {
  background: #fff;
  cursor: pointer;
  width: 400px;
}

.dropdown-menu li:hover {
  background: #fff;
  color: #101522;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: grey;
  font-size: 14px;
  padding: 16px;
}
